.main-screen {
    margin-bottom: 40px;
    padding: 32px 0;
    background-color: var(--color-primary);

    @media(max-width:1024px) {
        margin-bottom: 32px;
    }

    &__body {
        padding: 32px;
        border-radius: 12px;
        background-color: #ffffff;

        @media(max-width:1024px) {
            padding: 16px;
        }

        .row {
            @media(max-width:1024px) {
                .col-md-6 {
                    &:first-child {
                        order:2;
                    }
    
                    &:last-child {
                        order:1;
                    }
                }
            }
        }
    }

    &__info {
       
    }

    &__txt {
        margin-bottom: 24px;
        color: var(--color-txt-dark);
    }

    &__image {
        text-align: center;
        
        @media(max-width:1024px) {
            margin-bottom: 16px;
        }

        img {
            // width: 100%;
        }
    }

    &__btns {
        &:first-child {
            margin-right: 10px;
        }

        @media(max-width:1024px) {
            display: flex;
            justify-content: space-between;
        }
    }

    &__wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;

        h1 {
            color: var(--color-txt-dark);
        }
    }

    ul li{
        position: relative;
        margin-bottom: 10px;
        padding-left: 30px;
        line-height: 1.6;

        &::before {
          position: absolute;
          display: inline-block;  
          content: '';
          left: 0;
          width: 24px;
          height: 24px;
          background-color: var(--color-primary);
          border-radius: 50%;
        }
    }

    ol {
        position: relative;
        list-style: none;
        list-style-type: lower-greek;
        counter-reset: my-awesome-counter;
      }
      ol li {
        counter-increment: my-awesome-counter;
        padding-left: 30px;
        margin-bottom: 16px;
      }
      ol li::before {
        content: counter(my-awesome-counter);
        position: absolute;
        display: inline-block;  
        left: 0;
        width: 24px;
        height: 24px;
        background-color: var(--color-primary);
        border-radius: 50%;
        color: #ffffff;
        text-align: center;
        line-height: 1.5;
      }
}